* {
	box-sizing: border-box;
}

html {
	height: 100%;
}

body {
	font-family: $arial;
	height: 100%;
}
a:focus {
	outline: none;
}
ul {
	list-style: none;
}

.main-wrapper {
	overflow: hidden;
	position: relative;
	min-height: 100%;
}

.bg-cover {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}

.flex-wrap {
	-webkit-flex-wrap: wrap;
	-moz-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	-o-flex-wrap: wrap;
	flex-wrap: wrap;
}

.d-flex {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
}

.align-center {
	align-items: center;
}

.justify-center {
	justify-content: center;
}
.container {
	max-width: 1320px;
	width: 100%;
}
.common-btn-blue:focus {
	outline: none;
}

.common-btn-blue:hover {
	background-color: $cobalt-blue;
}

.common-btn-blue {
	text-decoration: none;
	background-color: $sky-blue;
	border: 0;
	text-align: center;
	border-radius: 50px;
	padding: 6px 15px;
	min-width: 171px;
	font-family: $gibson;
	text-transform: uppercase;
	margin-bottom: 10px;
	transition: 0.4s all ease-in-out;
	cursor: pointer;
	position: relative;
	overflow: hidden;
	outline: none;

	span {
		font-size: 20px;
		color: $white;
		line-height: 46px;
		letter-spacing: 0.04px;
		font-family: $gibson;
		font-weight: $font-semi-bold;
		position: relative;
		z-index: 1;

		@media screen and (max-width:$mobile-small) {
			font-size: 16px;
			line-height: 39px;
		}
	}

	&:after {
		content: " ";
		display: block;
		width: 170px;
		height: 70px;
		position: absolute;
		top: 60px;
		/*transform: translateY(-70px);	
			-webkit-transform: translateY(-70px);*/
		left: 0;
		right: 0;
		transition: 0.4s all ease-in-out;
		background-image: url(#{$imgpath}/button-hover-effect.svg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
	}

	@media screen and (max-width:$large-m) {
		min-width: 150px;
		padding: 4px 10px 2px;
	}

	@media screen and (max-width:$ipad-landscape) {
		min-width: 130px;
	}

	&:hover {
		&:after {
			transform: translateY(-75px);
			-webkit-transform: translateY(-75px);
		}
	}
}

/*White Button*/
.common-btn-white {
	border:1px solid $panton-gray;
	background-color: $white;
	text-align: center;
	border-radius: 50px;
	padding: 11px 15px;
	min-width: 145px;
	text-transform: uppercase;
	outline: none;
	transition: 0.4s all ease-in-out;
	cursor: pointer;
	position: relative;
	overflow: hidden;
	outline: none;
	span {
		color: $sky-blue;
		font-size: 14px;
		line-height: 16px;
		font-family: $gibson;
		font-weight: $font-semi-bold;
		position: relative;
		z-index: 1;

		@media screen and (max-width:$mobile-small) {
			font-size: 16px;
			line-height: 39px;
		}
	}
	
	&:after {
		content: " ";
		display: block;
		width: 170px;
		height: 70px;
		position: absolute;
		top: 60px;
		/*transform: translateY(-70px);	
			-webkit-transform: translateY(-70px);*/
		left: 0;
		right: 0;
		transition: 0.4s all ease-in-out;
		background-image: url(#{$imgpath}/button-hover-sky-blue.svg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
	}


	@media screen and (max-width:$ipad-landscape) {
		min-width: 130px;
	}
	@media screen and (max-width:$mobile-small) {
		padding: 4px 15px;
	}

	&:hover {
		&:after {
			transform: translateY(-75px);
			-webkit-transform: translateY(-75px);
		}
	}
	
	&:hover {
		background-color: $sky-blue;
		border-color: $sky-blue;
		span  {
			color: $white;	
		}
	}
	
}

/*White Button*/
