	background-color: #00447A;
	text-align: center;
	border-radius: 50px;
	padding: 10px 15px;
	min-width: 106px;
	text-transform: uppercase;
	outline: none;
	transition: 0.4s all ease-in-out;
	cursor: pointer;
	position: relative;
	overflow: hidden;
	outline: none;
	@media screen and (max-width: $mobile-small) {
		padding: 4px 15px;
	}
	span {
		color: $white;
		font-size: 14px;
		line-height: 16px;
		font-family: $gibson;
		font-weight: $font-semi-bold;
		position: relative;
		z-index: 1;

		@media screen and (max-width:$mobile-small) {
			font-size: 16px;
			line-height: 39px;
		}
	}
	
	&:after {
		content: " ";
		display: block;
		width: 170px;
		height: 70px;
		position: absolute;
		top: 60px;
		/*transform: translateY(-70px);	
			-webkit-transform: translateY(-70px);*/
		left: 0;
		right: 0;
		transition: 0.4s all ease-in-out;
		background-image: url(#{$imgpath}/button-hover-sky-white.svg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
	}


	@media screen and (max-width:$ipad-landscape) {
		min-width: 130px;
	}

	&:hover {
		&:after {
			transform: translateY(-75px);
			-webkit-transform: translateY(-75px);
		}
	}
	
	&:hover {
		background-color: $white;
		border-color: $white;
		span  {
			color: $cobalt-blue;	
		}
	}
	
}

a.btn, button.btn{-webkit-appearance: none; -moz-appearance: none; appearance: none; }
	
/*For upload custom button*/
.upload-btn-wrapper {
	position: relative;
	overflow: hidden;
	display: inline-block;
	cursor: pointer;

	.btn {
		color: $white;
		cursor: pointer;
		min-width: 138px;
		padding: 7px 10px;
		margin-bottom: 0;
		span {
			font-size: 16px;
			color: $white;
			line-height: 28px;
			font-family: $gibson;
			font-weight: $font-semi-bold;
			position: relative;
			z-index: 1;
			@media screen and (max-width:$mobile-small) {
			font-size: 12px;
			line-height: 29px;
			}
		}

		@media screen and (max-width:$mobile-small) {
			padding: 4px 10px 2px;
			min-width: 101px;
		}
	}

	input[type=file] {
		font-size: 100px;
		position: absolute;
		right: 0;
		top: 0;
		opacity: 0;
		cursor: pointer;
		z-index: 2;

		&:hover {
			+ button {
				cursor: pointer;

				&:after {
					transform: translateY(-75px);
					-webkit-transform: translateY(-75px);
				}
			}
		}
	}
}

/*Common form field css*/
form {

	label {
		display: block;
		width: 100%;
		margin-bottom: 3px;
		font-size: 16px;
		line-height: 20px;
		font-family: $gibson;
		font-weight: $font-normal;
		color: $black;
	}

	input[type="email"],
	input[type="password"],
	input[type="text"],
	textarea {
		font-size: 16px;
		padding: 8px 12px;
		width: 100%;
		line-height: 20px;
		border: 1px solid $panton-gray;
		font-family: $arial;
		font-weight: 400;
		color: $light-black;

		&:focus,
		&:focus {
			border-color: $sky-blue;
			outline: none;
		}

	}

}

/*Common form css*/
.form-block {
	.form-group {
		margin-bottom: 18px;
	}
}

/*For two column & full column*/
.row {
	margin: 0 -15px;
	display: flex;
	flex-wrap: wrap;

	.one-by-two {
		width: 50%;
		padding: 0 15px;

		@media screen and (max-width: $mobile-small) {
			width: 100%;
		}
	}

	.full {
		width: 100%;
		padding: 0 15px;
	}
}

/*Left fixed navbar*/
nav {
	&.fixed-left {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		width: 255px;
		padding: 48px 0 35px 0;
		overflow: hidden;
		z-index: 1;
		@media screen and (max-width:$large-m) {
			padding: 25px 0 35px 0;
		}
		@media screen and (max-width:$large) {
			position: absolute;
		}
		@media screen and (max-width:$mobile-small) {
			width: 100%;
			top: auto;
			z-index: 2;
			padding: 13px 0 6px;
			position: fixed;  
		}

		&:before {
			content: "";
			background: transparent linear-gradient(270deg, #00738100 0%, #003A41 100%) 0% 0% no-repeat;
			opacity: 0.25;
			height: 100%;
			width: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}

		/*&:after {
		content: "";
		background: -moz-linear-gradient(left, rgba(0, 110, 124, 0) 0%, rgba(0, 98, 110, 0.02) 23%, rgba(0, 85, 95, 0.06) 48%, rgba(0, 73, 81, 0.13) 72%, rgba(0, 58, 65, 0.25) 100%) background: -webkit-linear-gradient(left, rgba(0, 110, 124, 0) 0%, rgba(0, 98, 110, 0.02) 23%, rgba(0, 85, 95, 0.06) 48%, rgba(0, 73, 81, 0.13) 72%, rgba(0, 58, 65, 0.25) 100%);
		background: linear-gradient(to right, rgba(0, 110, 124, 0) 0%, rgba(0, 98, 110, 0.02) 23%, rgba(0, 85, 95, 0.06) 48%, rgba(0, 73, 81, 0.13) 72%, rgba(0, 58, 65, 0.25) 100%);
		display: block;
		position: absolute;
		left: 0;
		top:0;
		width: 100%;
		height: 100%;
	}*/

		.navbar-logo {
			display: block;
			width: 150px;
			margin: 0 auto;
			position: relative;

			@media screen and (max-width:$large-m) {
				width: 120px;
			}

			@media screen and (max-width:$mobile-small) {
				display: none;
			}

			img {
				width: 100%;
			}
		}

		ul {
			padding-left: 0;
			margin-top: 95px;
			margin-bottom: 0;
			@media screen and (max-width:$large-m) {
				margin-top: 30px;
			}
			@media screen and (max-width:$mobile-small) {
				margin-top: 0;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-wrap: wrap;
			}

			li {
				&:not(:last-child)
				{
					margin-bottom: 13px;	
					@media screen and (max-width:$mobile-small) {
						margin-bottom: 0;	
					}
				}
				a {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					padding: 15px 0 15px 45px;
					position: relative;
					outline: none;
					position: relative;
					transition: 0.5s all;
					text-decoration: none;

					@media screen and (max-width:$large-m) {
						padding: 10px 0 10px 40px;
					}

					@media screen and (max-width:$mobile-small) {
						padding: 0;
						justify-content: center;
					}

					svg {
						.shp0 {
							transition: 0.5s all;
						}
					}

					&:hover {
						svg {
							.shp0 {
								fill: $sky-blue;
							}
						}
					}

					&.active {
						span {
							color: $white;
						}
						svg {
							.shp0 {
								fill: $sky-blue;
							}
						}
					}

					span {
						color: $white-transparent-menu-text;
						text-decoration: none;
						font-family: $gibson;
						font-weight: $font-normal;
						font-size: 20px;
						line-height: 28px;
						padding-left: 20px;
						outline: none;

						> span {
							padding-left: 0;

							@media screen and (max-width:$mobile-small) {
								display: none;
							}
						}

						@media screen and (max-width:$large-m) {
							font-size: 18px;
						}

						@media screen and (max-width:$mobile-small) {
							font-size: 14px;
							line-height: 28px;
							width: 100%;
							padding-left: 0;
							text-align: center;
						}
						@media screen and (max-width:$small-x) {
							font-size: 11px;
							line-height: 22px;
						}
					}

					img {
						width: 23px;
						height: auto;
						outline: none;
					}

					svg {
						max-width: 23px;
						height: auto;
						outline: none;
						@media screen and (max-width:$large-m) {
							max-width: 20px;
						}
						@media screen and (max-width:$mobile-small) {
							max-width: 26px;
						}
						@media screen and (max-width:$small-x) {
							max-width: 20px;
						}
						/*IE Support CSS */
						@media all and (-ms-high-contrast:none) {
							height: 23px;
						}

						@media screen and (max-width:$mobile-small) {
							max-height: 22px;
						}
					}
					label {
						height: 12px;
						width: 12px;
						background-color: $red;
						border-radius: 50%;
						overflow: hidden;
						color: $white;
						font-family: $arial;
						font-weight: $font-normal;
						font-size: 8px;
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						left: 54px;
						bottom: 10px;
						@media screen and (max-width:$mobile-small) {
							left: 36px;
							bottom: 22px;
						}
					}
					&:after {
						content: "";
						display: block;
						width: 0%;
						height: 100%;
						border-right: 5px solid $sky-blue;
						position: absolute;
						right: 0;
						top: 0;
						opacity: 0;
						visibility: hidden;

						background: -moz-linear-gradient(left, rgba(128, 128, 128, 0.02) 0%, rgba(198, 198, 198, 0.09) 55%, rgba(255, 255, 255, 0.2) 100%);

						background: -webkit-linear-gradient(left, rgba(128, 128, 128, 0.02) 0%, rgba(198, 198, 198, 0.09) 55%, rgba(255, 255, 255, 0.2) 100%);

						background: linear-gradient(to right, rgba(128, 128, 128, 0.02) 0%, rgba(198, 198, 198, 0.09) 55%, rgba(255, 255, 255, 0.2) 100%);
						transition: 0.5s all;
					}

					&:hover {
						&:after {
							opacity: 1;
							visibility: visible;
							width: 100%;

							@media screen and (max-width:$mobile-small) {
								display: none;
							}
						}
					}

					&.active {
						&:after {
							opacity: 1;
							visibility: visible;
							width: 100%;

							@media screen and (max-width:$mobile-small) {
								display: none;
							}
						}
					}


				}
			}
		}

		.nav-copyright {
			color: $white;
			font-size: 10px;
			line-height: 24px;
			font-family: $arial;
			font-weight: $font-normal;
			position: absolute;
			bottom: 25px;
			padding-left: 23px;
			z-index: 1;

			@media screen and (max-width:$mobile-small) {
				display: none;
			}
			@media screen and (max-width:$large-m) {
				bottom: 5px;
			}
			@media screen and (max-width:$ipad-portrait) {
				bottom: 15px;
			}
		}
		&.white-bg-trnsparent
		{
			@media screen and (max-width:$mobile-small) {
				background-color: $light-black;
			}
		}
		.pattern-block {
			position: absolute;
			transform: rotate(6deg);
			-webkit-transform: rotate(6deg);
			right: -83px;
			bottom: -18px;
			z-index: -1;
			@media screen and (max-width:$mobile-small) {
				display: none;
			}
		}
		
	}
}

/*Common right block*/
.right-content-part {
	width: calc(100% - 255px);
	padding-left: 90px;
	margin-left: auto;
	align-items: flex-start;
	@media screen and (max-width:$large-mx) {
		padding-left: 40px;
	}

	@media screen and (max-width:$ipad-landscape) {
		padding: 0 12px;
	}

	@media screen and (max-width:$mobile-small) {
		width: 100%;
		padding: 0 0 72px 0;
	}

	/*Top Heading block*/
	.top-heading-continer { max-width: 1320px; width: 100%;  }
	.top-heading {
		background: -moz-linear-gradient(left, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 40%, rgba(254, 255, 255, 1) 42%, rgba(252, 254, 253, 1) 42%, rgba(253, 254, 255, 1) 45%, rgba(246, 251, 254, 1) 66%, rgba(246, 251, 254, 1) 100%);

		background: -webkit-linear-gradient(left, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 40%, rgba(254, 255, 255, 1) 42%, rgba(252, 254, 253, 1) 42%, rgba(253, 254, 255, 1) 45%, rgba(246, 251, 254, 1) 66%, rgba(246, 251, 254, 1) 100%);

		background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 40%, rgba(254, 255, 255, 1) 42%, rgba(252, 254, 253, 1) 42%, rgba(253, 254, 255, 1) 45%, rgba(246, 251, 254, 1) 66%, rgba(246, 251, 254, 1) 100%);
		width: 100%;
		border-bottom: 1px solid $off-white;
		padding: 36px 274px 36px 0;

		@media screen and (max-width:$large-mx) {
			padding: 12px 80px 12px 0;
			width: calc(100% - 15px);
    		margin-left: auto;
		}
		
		@media screen and (max-width:$ipad-portrait) {
			padding: 12px 15px 12px 0;
			width: 100%;
		}

		@media screen and (max-width:$mobile-small) {
			padding: 2px 30px;
		}
		
		.name-of-heading {
			h1 {
				font-size: 40px;
				line-height: 46px;
				margin: 0;
				color: $cobalt-blue;
				font-family: $gibson;
				font-weight: $font-semi-bold;

				@media screen and (max-width:$large-m) {
					font-size: 30px;
					line-height: 36px;
				}

				@media screen and (max-width:$mobile-small) {
					font-size: 20px;
					line-height: 46px;
					margin: 0;
				}
			}

			img {
				margin-right: 15px;
				max-width: 37px;
				@media screen and (max-width:$large-m) {
					max-width: 30px;
				}
				@media screen and (max-width:$mobile-small) {
					max-width: 23px;
				}
			}
		}

		/*Profile progressbar css*/
		.progressbar-block {
			max-width: 327px;
			width: 100%;
			margin-left: auto;
			padding-right: 10px;
			position: relative;
			top: -8px;
			@media screen and (max-width:$large-m) {
				top: -4px;
			}
			@media screen and (max-width:$ipad-landscape) {
				top: 0;
			}
			@media screen and (max-width:$mobile-small) {
				max-width: 110px;
			}

			.percent-text {
				font-size: 18px;
				line-height: 22px;
				color: $panton-gray;
				font-family: $gibson;
				font-weight: $font-normal;
				margin-bottom: 7px;
				display: block;

				@media screen and (max-width:$mobile-small) {
					font-size: 12px;
					line-height: 22px;
				}
			}

			.progress-bar-wrap {
				position: relative;
				width: 100%;
				height: 10px;
				background: $off-white;
				border-radius: 5px;
				overflow: hidden;

				.p {
					visibility: hidden;
				}

				.progress-bar {
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 10px;
					background: $sky-blue;
					border-radius: 5px;
					transform: translateX(-100%);
				}
			}

		}
	}

	/*Profile bottom details*/
	.bottom-content-block {
		padding: 40px 0;
		width: 100%;
		@media screen and (max-width:$large-mx) {
			padding: 15px;
		}
		@media screen and (max-width:$ipad-landscape) {
			padding: 24px 0;
		}
		@media screen and (max-width:$large-x) {
			padding: 20px 0;
		}
		&.with-filter {
			padding: 15px 0;
			@media screen and (max-width:$large-mx) {
				padding: 15px;
			}
			@media screen and (max-width:$mobile-small) {
				padding: 15px 0;
			}
		}
	}

}

