		}
	}
	
	.drop-down-menu
	{
		position: absolute;
		top: 43px;
		height: 0;
		visibility: hidden;
		opacity: 0;
		background-color: $white;
		right: 0;
		left: 0;
		text-align: right;
		ul 
	{
		padding-left: 0;
