/*Filter Block*/
.fileter-block { 
	max-width: 1320px;
	width: 100%;
	@media screen and (max-width:$mobile-small) {
		padding: 0 23px;
	}
	.search-sort-block
	{
		margin-left: auto;
		@media screen and (max-width:$large-x) {
				margin-top: 5px;
		}
		@media screen and (max-width:$ipad-portrait) {
			margin-left: 0;
    		margin-right: auto;
		}
		@media screen and (max-width:$mobile-small) {
			width: 100%;
			margin-top: 0;
		}
	}
	&.border-bottom {
		padding-bottom: 20px;
		border-bottom: 1px solid $off-white;
		position: relative;
		&:after {
			content: "";
			display: block;
			width: 100%;
			height: 1px;
			background-color: $off-white;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			box-shadow: 0px 1px 15px rgba(0,0,0,0.16);
		}
	}
}

/*Auto search box*/
.auto-search-box 
{
	position: relative;
	width: 238px;
    padding-right: 20px;
	margin-left: auto;
	@media screen and (max-width:$large-m) {
		width: 200px;
	}
	@media screen and (max-width:$mobile-small) {
		width: 268px;
		padding: 0;
		margin-right: auto;
		margin-left: 0;
	}
	@media screen and (max-width:$small-x) {
		width: 200px;
	}
	input[type="text"]
	{
		border: 1px solid #f5f5f5;
    	position: relative;	
		font-size: 16px;
			line-height: 22px;
			padding: 11px 25px 11px 33px;
			width: 100%;
			display: block;
			font-family: $gibson;
			font-weight: $font-normal;
			color: $panton-gray;
			text-decoration: none;
			background-image: url(#{$imgpath}/ic_search.svg);
			background-size: 17px;
			background-repeat: no-repeat;
			background-position: 5% center;
		@media screen and (max-width:$mobile-small) {
			padding: 5px 9px 5px 35px;
		}
	}
}

/*Mobile filter block open close*/
.mobile-filter {
	@media screen and (min-width:$mobile-small-up) {
			display: none;
	}
	.filter-open-btn {
		margin-left: auto;
		width: 16px;
		img, svg {
			width: 100%;
		}
	}
	.open-close-filter-block
	{
		position: fixed;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		background-color: $white;
		z-index: 9;
		transform: translateY(100%);
		-webkit-transform: translateY(100%);
    	transition: 0.3s all ease-in-out;
   		overflow-y: scroll;
		.top-head {
			border-bottom: 1px solid $off-white;
			padding: 5px 30px;
			.top-title
			{
				min-width: 120px;
				img {
					width: 25px;
				}
				h4 {
					font-size: 20px;
					line-height: 46px;
					font-family: $gibson;
					font-weight: $font-semi-bold;
					color: $cobalt-blue;
					margin: 0;
					padding-left: 10px;
				}
			}
		}
	}
	.list-filter-mobile {
		padding: 0 40px 40px 40px;
		h5 {
					font-size: 18px;
					line-height: 28px;
					font-family: $gibson;
					font-weight: $font-semi-bold;
					color: $cobalt-blue;
					margin: 33px 0 0 0;
		}
		ul {
			padding-left: 0;
			margin-top: 0;
			li {
				a {
					font-size: 15px;
					line-height: 22px;
					font-family: $gibson;
					font-weight: $font-normal;
					color: $panton-gray;
					text-decoration: none;
					padding: 10px 25px 10px 0;
					display: block;
					width: 100%;
					position: relative;
				}
				&.active
				{
					a {
						color: $black;
						&:after {
							content: "";
							display: inline-block;
							background-image: url(#{$imgpath}/ic_right.svg);
							background-size: contain;
							background-repeat: no-repeat;
							background-position: right center;
							height: 10px;
							width: 12px;
							position: absolute;
							right: 15px;
							top: 50%;
							transform: translateY(-50%);
							-webkit-transform: translateY(-50%);
						}
					}
				}
				&:not(last-child)
				{
					border-bottom: 1px solid $off-white;
				}
			}
		}
	}
	&.filter-active {
		.open-close-filter-block {
			transform: translateY(0%);
			-webkit-transform: translateY(0%);
		}
	}
	.btn-block {
		button {
			min-width: 195px;
			margin-bottom: 0;
			padding: 5px 10px 3px;
		}
	}
}


